import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Autoplay } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "../css/Product.css"; // CSS  

const products = [
  { id: 1, name: "", image: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg" },
  { id: 2, name: "", image: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg" },
  { id: 3, name: "", image: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg" },
  { id: 4, name: "", image: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg" },
  { id: 5, name: "", image: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg" }
];

const Product = () => {
  return (
    <div className="product-container">
      <h1> </h1>
      <Swiper
        modules={[Navigation, Pagination, Autoplay]}
        spaceBetween={20}
        slidesPerView={4}
        navigation
        pagination={{ clickable: true }}
        autoplay={{ delay: 3000, disableOnInteraction: false }}
        loop={true}
      >
        {products.map((product) => (
          <SwiperSlide key={product.id}>
            <div className="product-card">
              <img src={product.image} alt={product.name} />
              <h3>{product.name}</h3>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
};

export default Product;
