import React from "react";
import Slider from "react-slick";
import { motion } from "framer-motion";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "../css/MainPage.css";
import product1 from "../assets/1.jpg";
import product2 from "../assets/2.jpg";
import product3 from "../assets/3.jpg";
import product4 from "../assets/4.jpg";
import special1 from "../assets/slide1.jpg";
import special2 from "../assets/slide1.jpg";
import special3 from "../assets/slide1.jpg";

const MainPage = () => {
  //   
  const productSliderSettings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    arrows: true
  };

  const products = [
    { id: 1, name: "Product 1", image: product1 },
    { id: 2, name: "Product 2", image: product2 },
    { id: 3, name: "Product 3", image: product3 },
    { id: 4, name: "Product 4", image: product4 }
  ];

  const specialBoxes = [
    { id: 1, title: "Special Box 1", image: special1 },
    { id: 2, title: "Special Box 2", image: special2 },
    { id: 3, title: "Special Box 3", image: special3 }
  ];

  return (
    <div className="main-page">
      <h1>Belif Online Experience</h1>

      {/*    */}
      <div className="product-slider-container">
        <h2>Our Products</h2>
        <Slider {...productSliderSettings} className="product-slider">
          {products.map((product) => (
            <motion.div
              key={product.id}
              className="product-item"
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{ duration: 0.5 }}
            >
              <img src={product.image} alt={product.name} />
              <p>{product.name}</p>
            </motion.div>
          ))}
        </Slider>
      </div>

      {/*   */}
      <div className="special-section">
        <h2>Special Boxes</h2>
        <div className="special-boxes">
          {specialBoxes.map((box) => (
            <motion.div
              key={box.id}
              className="special-box"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: box.id * 0.2 }}
            >
              <div className="image-container">
                <img src={box.image} alt={box.title} />
              </div>
              <h3>{box.title}</h3>
            </motion.div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default MainPage;
