import React, { useState } from "react";
import "../css/Gallery.css"; // CSS  

const images = [
  { id: 1, src: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg?sig=1", alt: "Gallery Image 1" },
  { id: 2, src: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg?sig=2", alt: "Gallery Image 2" },
  { id: 3, src: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg?sig=3", alt: "Gallery Image 3" },
  { id: 4, src: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg?sig=4", alt: "Gallery Image 4" },
  { id: 5, src: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg?sig=5", alt: "Gallery Image 5" },
  { id: 6, src: "https://image.ytn.co.kr/general/jpg/2024/0903/202409030952220687_d.jpg?sig=6", alt: "Gallery Image 6" }
];

const Gallery = () => {
  const [selectedImage, setSelectedImage] = useState(null);

  return (
    <div className="gallery-container">
      <h1></h1>
      <div className="gallery-grid">
        {images.map((image) => (
          <img
            key={image.id}
            src={image.src}
            alt={image.alt}
            className="gallery-item"
            onClick={() => setSelectedImage(image.src)}
          />
        ))}
      </div>

      {/*  (  ) */}
      {selectedImage && (
        <div className="modal" onClick={() => setSelectedImage(null)}>
          <div className="modal-content">
            <img src={selectedImage} alt="Selected" />
          </div>
        </div>
      )}
    </div>
  );
};

export default Gallery;
